
import "@/sass/globals.scss";

import Vue from "vue";
import { Component } from "vue-property-decorator";
import { regeneratePostsCache } from "@/lib/wordpress/regenerate-posts-cache";
import RegeneratingCacheProgress from "@/components/Notifications/RegeneratingCacheProgress.vue";
import Notifications from "@/components/Notifications/Notifications.vue";

@Component({
  components: { Notifications, RegeneratingCacheProgress }
})
export default class extends Vue {
  regenerating = false;

  async regenerateCache() {
    this.regenerating = true;
    await regeneratePostsCache();
    this.regenerating = false;
  }
}
